<template>
  <div class="login_container">
    <div>
      <button @click="test1">题</button>
      <div v-for="(item,index) in test" :key="index">
        <img :src="item.img" alt />
      </div>
      <!-- <img src="http://localhost:3002/img/d1.jpg" alt /> -->
    </div>
    <div class="login_box">
      <!-- 头像区域 -->
      <div class="avatar_box">
        <img src="../assets/logo.png" alt />
      </div>
      <!-- 头像区域结束 -->
      <!-- 表单区域 -->
      <el-form
        ref="loginFormRef"
        :model="loginForm"
        :rules="loginFormRules"
        label-width="0px"
        class="login_form"
      >
        <!-- 用户名 -->
        <el-form-item prop="account">
          <el-input type="text" v-model="loginForm.account" prefix-icon="el-icon-user"></el-input>
        </el-form-item>
        <!-- 密码 -->
        <el-form-item prop="password">
          <el-input type="password" v-model="loginForm.password" prefix-icon="el-icon-lock"></el-input>
        </el-form-item>
        <!-- 按钮 -->
        <el-form-item class="btn">
          <el-button type="primary" @click="login">登录</el-button>
          <el-button type="info" @click="resetLoginForm">重置</el-button>
          <el-button type="success" @click="dialog = true">注册</el-button>
        </el-form-item>
      </el-form>
      <!-- 表单区域结束 -->
    </div>
    <el-drawer
      title="您对涪陵外卖的支持，会成为外卖前进的动力！"
      :before-close="handleClose"
      :visible.sync="dialog"
      direction="ltr"
      custom-class="demo-drawer"
      ref="drawer"
      size="35%"
    >
      <div class="demo-drawer__content">
        <h3 style="font-size:30px;color:green;margin-bottom:50px;text-align:center">欢迎来到注册页面</h3>
        <el-form :model="form" status-icon :rules="rules" ref="zhuceRuleForm" class="zhuce">
          <el-form-item label="用户名：" prop="username" :label-width="formLabelWidth">
            <el-input v-model="form.username" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="账号：" prop="account" :label-width="formLabelWidth">
            <el-input v-model="form.account" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="密码：" prop="password" :label-width="formLabelWidth">
            <el-input type="password" v-model="form.password" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="确认密码：" prop="checkPass" :label-width="formLabelWidth">
            <el-input type="password" v-model="form.checkPass" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="手机号：" prop="phone" :label-width="formLabelWidth">
            <el-input type="text" v-model="form.phone" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label prop="checkphone" :label-width="formLabelWidth">
            <el-input
              type="text"
              v-model="form.checkphone"
              autocomplete="off"
              placeholder="验证码"
              class="receiveCode"
            ></el-input>
            <el-button
              type="primary"
              class="sendCode"
              :disabled="isDisabled"
              @click="sendCheak"
            >{{buttonText}}</el-button>
          </el-form-item>
          <el-form-item :label-width="formLabelWidth" prop="checkImg">
            <el-input
              class="receiveCode"
              v-model="form.checkImg"
              ref="checkImgRef"
              placeholder="请输入验证码"
            ></el-input>
            <img
              src="http://localhost:3002/getCaptcha"
              ref="checkImgRef"
              @click.prevent="editChexkImg"
              alt="captcha"
            />
          </el-form-item>
        </el-form>
        <div class="demo-drawer__footer">
          <el-button @click="cancelForm" type="info" class="quxiao">取 消</el-button>
          <el-button
            type="primary"
            @click="zhuce"
            :loading="loading"
            class="sendCode"
          >{{ loading ? '提交中 ...' : '确 定' }}</el-button>
        </div>
      </div>
    </el-drawer>
  </div>
</template>

<script>
//自定义手机验证
var validatePhone = (rule, value, callback) => {
  const regPhone = /^(0|86|17951)?(13[0-9]|15[0123456789]|17[678]|18[0-9]|14[57])[0-9]{8}$/;
  if (!value) {
    return callback(new Error("手机号不能为空"));
  } else {
    setTimeout(() => {
      if (!regPhone.test(value)) {
        return callback(new Error("手机号码格式不对"));
      } else {
        callback();
      }
    }, 1000);
  }
};

export default {
  data() {
    // 自定义确认密码规则
    var validatepassword = (rule, value, callback) => {
      console.log(this.form);
      if (value !== this.form.password) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    //自定义输入验证码规则
    var checkSendPhone = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入手机验证码"));
      } else {
        callback();
      }
    };
    //自定义规则检验图片验证码规则
    var checkImgWith = (rule, value, callback) => {
      console.log(document.cookie);
      // console.log(document.cookie.captcha);
      // console.log(document.cookie.split("=")[1]);
      let getImgContent = document.cookie.split("=")[1];
      console.log(getImgContent);
      if (this.form.checkImg !== getImgContent) {
        callback(new Error("验证码错误"));
      } else {
        callback();
      }
    };
    return {
      // { img: require("../assets/img/d1.jpg") },
      // { img: require("../assets/img/d1.jpg") }
      test: [],
      buttonText: "发送验证码",
      isDisabled: false, //是否禁止发送验证码
      flag: true,
      // 注册框显示隐藏信息
      table: false,
      dialog: false,
      loading: false,
      // 注册表单信息
      form: {
        checkphone: "15023502332",
        account: "123",
        phone: "15023509822",
        username: "2123",
        password: "1231233",
        checkPass: "1231233",
        checkImg: "",
      },
      // 注册规则
      rules: {
        username: [
          { required: true, message: "用户名不能为空", trigger: "blur" },
        ],
        account: [
          { required: true, message: "请输入账号", trigger: "blur" },
          { min: 3, message: "长度在6~20个字符", max: 20, trigger: "blur" },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          { min: 6, message: "长度在6~20个字符", max: 20, trigger: "blur" },
        ],
        checkPass: [
          { required: true, message: "请再次输入密码", trigger: "blur" },
          { validator: validatepassword, trigger: "blur" },
        ],
        phone: [
          { required: true, message: "手机号不能为空", trigger: "blur" },
          { validator: validatePhone, trigger: "blur" },
        ],
        checkphone: [{ validator: checkSendPhone, trigger: "blur" }],
        checkImg: [
          { required: true, message: "请填写图片中内容", trigger: "blur" },
          { validator: checkImgWith, trigger: "blur" },
        ],
      },
      formLabelWidth: "100px",
      timer: null,
      //这是登录表单的数据绑定对象，里面是该绑定对象的值
      loginForm: {
        account: "ych",
        password: "123456",
      },
      //表单的验证规则对象
      loginFormRules: {
        //验证用户名合法性
        account: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          { min: 3, max: 6, message: "长度在 3 到 5 个字符", trigger: "blur" },
        ],
        //验证密码合法性
        password: [
          { required: true, message: "请输入登录密码", trigger: "blur" },
          {
            min: 6,
            max: 15,
            message: "长度在 6 到 15 个字符",
            trigger: "blur",
          },
        ],
      },
    };
  },
  methods: {
    async test1() {
      const { data: res } = await this.$http.get("test");
      this.test = res;
      console.log(this.test);
      this.test.forEach((data, index) => {
        console.log(data.img);
      });
    },
    //发送验证码
    sendCheak() {
      let tel = this.form.phone;
      console.log(tel);
      var time = 6;
      this.isDisabled = true;
      this.buttonText = "已发送";
      if (this.flag) {
        this.flag = false;
        let timer = setInterval(() => {
          time--;
          this.buttonText = time + "秒";
          if (time === 0) {
            clearInterval(timer);
            this.buttonText = "重新获取";
            this.isDisabled = false;
            this.flag = true;
          }
        }, 1000);
      }
    },
    //刷新更换验证码
    editChexkImg() {
      this.$refs.checkImgRef.src =
        "http://localhost:3002/getCaptcha?time=" + new Date();
    },
    //点击重置按钮 ，重置表单
    resetLoginForm() {
      this.$refs.loginFormRef.resetFields();
    },
    login() {
      this.$refs.loginFormRef.validate(async (valid) => {
        if (!valid) return;
        else {
          const res = await this.$http.post("login", this.loginForm);
          // console.log(res.status);
          if (res.status !== 200 || res.data == "fail") {
            console.log(res);
            return this.$message.error("登录失败！");
          } else {
            // console.log(res.data);
            // console.log(res);
            // console.log(res.data.token);
            this.$message.success("登录成功！");
            console.log(res.data);
            /*1.将登录后的token 保存到客户端的sessioonStorage中
							1.1项目中除了登录之外的其他api接口，必须在登录之后才能访问
							1.2token只在当前网站打开期间生效，所有将token保存在sessionStorage
							2.通过编程试导航跳转到后台主页，路由地址是/home
						*/
            window.sessionStorage.setItem(
              "token",
              // JSON.stringify([res.data.token, "ych"]),
              JSON.stringify({
                token: res.data.token,
                account: "ych",
                arr: [],
              })
            );
            // console.log(window.sessionStorage.getItem("token"));
            this.$router.push("/home");
          }
        }
      });
    },
    //注册功能
    handleClose(done) {
      if (this.loading) {
        return;
      }
      this.$confirm("确定要提交表单吗？")
        .then((_) => {
          this.$refs.zhuceRuleForm.validate((valid) => {
            if (!valid) return;
            this.$http.post("zhuce", this.form);
            this.$message.success("注册成功！");
            this.form = {};
            this.loading = true;
            this.buttonText = "发送验证码";
            this.timer = setTimeout(() => {
              done();
              // 动画关闭需要一定的时间
              setTimeout(() => {
                this.loading = false;
              }, 400);
            }, 2000);
          });
        })
        .catch((_) => {});
    },
    cancelForm() {
      this.loading = false;
      this.dialog = false;
      this.$refs.zhuceRuleForm.resetFields();
      this.buttonText = "发送验证码";
      clearTimeout(this.timer);
    },
    zhuce() {
      // this.$http.post("zhuce", this.form);
      this.$refs.drawer.closeDrawer();
      // this.form = {};
    },
  },
  created() {
    console.log(document.cookie);
  },
};
</script>

<style lang="stylus" scoped>
.receiveCode {
  width: 71%;
}

.sendCode {
  width: 100px;
}

html, body, #app {
  height: 100%;
}

.login_container {
  background-color: #2b4b6b;
  height: 100%;
}

.login_box {
  background-color: white;
  width: 450px;
  height: 300px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.avatar_box {
  padding: 10px;
  position: absolute;
  left: 50%;
  top: -50%;
  transform: translate(-50%, 50%);
  border: 1px solid #eee;
  border-radius: 50%;
  height: 130px;
  width: 130px;
  box-shadow: 0 0 10px #ddd;
  background-color: #fff;

  img {
    border-radius: 50%;
    width: 100%;
    height: 100%;
    background-color: #eee;
  }
}

.login_form {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 0 20px;
  box-sizing: border-box;
}

.btn {
  display: flex;
  justify-content: flex-end;
}

.quxiao {
  margin-left: 239px;
  width: 100px;
}

/deep/.header#el-drawer__title, /deep/.section.el-drawer__body {
  width: 84%;
}

.zhuce {
  width: 84%;
}
</style>
