<template>
  <el-container>
    <el-header>
      <el-page-header @back="goBack" content="我的购物车"></el-page-header>
    </el-header>
    <el-main>
      <el-card class="box-card">
        <el-table :data="mycartList" stripe style="width: 100%" border>
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column type="index" width="50"></el-table-column>
          <el-table-column prop="name" label="商品名称" width="280"></el-table-column>
          <el-table-column prop="price" label="价格" width="180"></el-table-column>
          <el-table-column prop="num" label="数量" width="180"></el-table-column>
          <el-table-column prop="totalPrice" label="总价"></el-table-column>
          <el-table-column fixed="right" label="操作" width="180">
            <template slot-scope="scope">
              <el-button @click="deleteGood(scope.row)" type="info" size="small">删除</el-button>
              <el-button type="primary" size="small" @click="pay(scope.row)">立即支付</el-button>
            </template>
          </el-table-column>
        </el-table>
      </el-card>
    </el-main>
  </el-container>
</template>
<script>
import { mapMutations, mapActions } from "vuex";
export default {
  data() {
    return {
      a: 12,
      payhowmush: 0,
      mycartList: [],
      freehowmush: 0,
      tableData: [
        {
          date: "2016-05-02",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1518 弄",
        },
        {
          date: "2016-05-04",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1517 弄",
        },
        {
          date: "2016-05-01",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1519 弄",
        },
        {
          date: "2016-05-03",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1516 弄",
        },
      ],
    };
  },
  methods: {
    ...mapActions(["pay"]),
    //  移出物品
    deleteGood(val) {
      // console.log(val.id);
      let token = JSON.parse(window.sessionStorage.getItem("token"));
      const del = token.arr.forEach((item, index) => {
        // console.log(item.id);
        if (item.id == val.id) {
          const b = token.arr.splice(index, 1);
          window.sessionStorage.setItem("token", JSON.stringify(token));
        } else {
          return item;
        }
      });
      this.getMycart();
    },
    // 获取购物车列表
    getMycart() {
      let token = JSON.parse(window.sessionStorage.getItem("token"));
      if (token.arr.id !== null) {
        this.mycartList = token.arr;
      }
    },
    // 回退
    goBack() {
      console.log("11");
      this.$router.go(-1);
    },

    pay(Goods) {
      this.$http.get("showfree").then((data) => {
        console.log(Goods);
        this.freehowmush = data.data;
        this.payhowmush = Goods.price - this.freehowmush;
        console.log(this.payhowmush);
        this.$confirm(
          `${
            "优惠卷便宜：" +
            this.freehowmush +
            "元" +
            "：实际支付：" +
            this.payhowmush +
            "元"
          }`,
          "立即支付",
          {
            confirmButtonText: "确定",
            content: this.payhowmush,
            cancelButtonText: "取消",
            type: "warning",
          }
        )
          .then(() => {
            this.$message({
              type: "success",
              message: "支付成功!",
            });
            this.$store.dispatch("pay", Goods);
            let token = JSON.parse(window.sessionStorage.getItem("token"));
            const del = token.arr.forEach((item, index) => {
              if (item.id == Goods.id) {
                const b = token.arr.splice(index, 1);
                window.sessionStorage.setItem("token", JSON.stringify(token));
              } else {
                return item;
              }
              this.getMycart();
            });
          })
          .catch(() => {
            this.$message({
              type: "info",
              message: "已取消支付",
            });
          });
      });
    },
  },
  created() {
    this.getMycart();
  },
};
</script>

